.formContainer {
  position: fixed;
  top: 0;bottom:0;right: 0;left: 0;
  z-index: 10;
  background-color: rgba(254, 255, 240, 0.87);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  transition: 0.4s;
  overflow: hidden;
}

.editing{
  background-color: rgba(46, 92, 28, 0.247) 
}

.hideContainer {
  transition: 0.3s;
  opacity: 0;
  pointer-events: none;
}

.closeIcon {
  position: absolute;
  top: 30px;
  right: 20px;
  transform: rotate(45deg);
  cursor: pointer;
  font-size: 1.8rem;
  color: #41403b8e;
  transition: 0.2s;
}

.closeIcon:hover {
  transition: 0.2s;
  color: #41403b;
}

.inputText {
  padding: 5px 10px;
  border: 1px solid #c4c3b5;
  border-radius: 5px;
  font-size: 18px;
  color: #5f5e5a;
}
